<script setup lang="ts">
import { useRouteQuery } from "@vueuse/router";
import useAsync from "~/composables/use-async";
import usePagination from "~/composables/use-pagination";
import { ORDER, SEGMENT_TRACK_EVENTS } from "~/constants";
import { useIdentityById } from "~/queries/identities";
import { useTeams } from "~/queries/teams";
import CategoriesAPI from "~/services/categories";
import EventsAPI from "~/services/events";

const { t } = useI18n();
const route = useRoute();
const router = useRouter();
const segment = useSegment();
const color = useColorMode();
const isMobile = useIsMobile();

const identityId = route.params.leagueId as string;
const runtimeConfig = useRuntimeConfig().public;
const selectedRoundId = useRouteQuery<string>("round-id", "all-rounds", { route, router });
const eventParams = { count: true, pageSize: 20, mainCategoryId: route.params.leagueId as string };

const { data: live, loading: loadingLive, run: loadLive } = useAsync(() => EventsAPI.load({ ...eventParams, type: "live" }));
const { data: upcoming, loading: loadingUpcoming, run: loadUpcoming } = useAsync(() => EventsAPI.load({ ...eventParams, type: "upcoming" }));
const { data: archived, run: loadArchived } = useAsync(() => EventsAPI.load({ ...eventParams, type: "archived" }));
const { data: league, isLoading } = useIdentityById(() => identityId);
const { data: teams, isLoading: loadingTeams } = useTeams(() => ({ identityId, pageSize: 100 }));

const banners = computed(() => {
  if (isMobile.value && league.value?.mobileBanners && league.value.mobileBanners.length > 0) {
    return league.value.mobileBanners;
  }

  return league.value?.banners;
});
const isDark = computed(() => color.value === "dark");
const customerRoundTitle = computed(() => runtimeConfig.APP_CUSTOMER.round_title ? runtimeConfig.APP_CUSTOMER.round_title : t("labels.all_rounds"));

const hasContent = computed(() => {
  const liveCount = live.value?.content?.count || 0;
  const upcomingCount = upcoming.value?.content?.count || 0;
  const archivedCount = archived.value?.content?.count || 0;

  return liveCount + upcomingCount + archivedCount !== 0;
});
const isEnabledTeams = computed(() => runtimeConfig.APP_CUSTOMER.enable_teams);

const onTeamClick = (team: IModelIdentityItem) => {
  segment.track(SEGMENT_TRACK_EVENTS.CLICK_ON_TEAM, {
    team_id: team.id,
    team_name: unref(team.title),
  });
};

const {
  items: allRounds,
  loading: loadingRounds,
  reset: reloadRounds,
} = usePagination<IModelLeagueRound>({
  pageSize: 60, // TODO: Consider having lazy loading inside of the BaseSelect.
  preloadedItems: [{ id: "all-rounds", title: customerRoundTitle }],
  loadItemsFn: (page: number, pageSize: number) => {
    return CategoriesAPI.subCategories.load({ page, pageSize, identityId, order: ORDER.desc });
  },
});

const loading = computed(() => isLoading.value || loadingTeams.value || loadingRounds.value);

onMounted(
  async () => {
    await Promise.all([
      loadArchived(),
      loadLive(),
      loadUpcoming(),
    ]);
    reloadRounds();

    if (!selectedRoundId.value)
      selectedRoundId.value = allRounds.value[0].id;
  },
);

watch(
  selectedRoundId,
  (newRoundId, _) => {
    if (newRoundId !== "all-rounds") {
      const selectedRound = allRounds.value.find(round => round.id === newRoundId);

      if (selectedRound) {
        segment.track(SEGMENT_TRACK_EVENTS.SELECT_ROUND, {
          round_id: selectedRound.id,
          round_name: unref(selectedRound.title),
        });
      }
    }
  },
);

definePageMeta({ layout: "home" });
</script>

<template>
  <div class="mb-12">
    <div>
      <template v-if="loading">
        <BlockIdentitySkeleton v-if="loading" />
        <BaseTeamSliderSkeleton v-if="isEnabledTeams" />
      </template>
      <template v-else-if="league">
        <BlockIdentity
          :title="league.title"
          :logo-url="league.logoUrl"
          :banners="banners"
          :facebook-url="league.facebookUrl"
          :x-url="league.xUrl"
          :instagram-url="league.instagramUrl"
          :web-url="league.webUrl"
        />
        <BaseTeamSlider v-if="teams && isEnabledTeams" :items="teams">
          <template #item="{ item }">
            <BaseButton
              class="flex-shrink-0"
              type="secondary"
              tag="nuxt-link"
              :name="`team-${item.id}`"
              :to="{ path: `/team/${item.id}` }"
              link
              @click.capture="() => onTeamClick(item)"
            >
              <BaseLogo
                v-tooltip="item.title"
                no-background
                :class="{ border: !isDark }"
                fit="contain"
                :icon-background="isDark ? '#2c374b' : '#ffffff'"
                :src="item.imageUrl"
                :width="48" :height="48"
                with-hover
                with-initials
                :title="item.title"
              />
            </BaseButton>
          </template>
        </BaseTeamSlider>
      </template>

      <div class="hr mb-2" role="separator" />
    </div>
    <div class="main-container !pb-6 text-main">
      <div>
        <ListEvents
          :title="t('labels.live')"
          :main-category-id="identityId"
          :items="live?.content.entries ?? []" :loading="loadingLive"
          type="live"
          has-see-all-link
          hide-on-no-content
          see-all-link-placement="top-right"
        >
          <template #end>
            <div class="hr my-16" role="separator" />
          </template>
        </ListEvents>
        <ListEvents
          :title="t('labels.upcoming')"
          :main-category-id="identityId"
          type="upcoming"
          :items="upcoming?.content.entries ?? []" :loading="loadingUpcoming"
          has-see-all-link
          hide-on-no-content
          see-all-link-placement="top-right"
        >
          <template #end>
            <div class="hr my-12" role="separator" />
          </template>
        </ListEvents>
      </div>

      <div class="flex items-center justify-between">
        <h3 class="text-heading-2 text-main">
          {{ t('labels.events') }}
        </h3>

        <BaseSelect
          v-model="selectedRoundId"
          :disabled="loadingRounds"
          :options="allRounds"
          name="rounds"
          options-key-value="id"
          emitted-key="id"
        />
      </div>
    </div>
    <template v-if="loading" />
    <template v-else-if="selectedRoundId === 'all-rounds'">
      <div v-if="hasContent">
        <div v-for="round in allRounds" :key="round.id">
          <template v-if="round.id !== 'all-rounds' && league?.id">
            <ListEvents
              class="!py-6"
              :main-category-id="league.id"
              :sub-category-id="round.id"
              load-when-visible
              group-by-key="date"
              hide-on-no-content
              group-by-date
              root-class="main-container"
            >
              <template #start>
                <div class="main-container text-heading-4-bold text-secondary !py-4" style="background: var(--base-card-bgColor)">
                  {{ round.title }}
                </div>
              </template>
            </ListEvents>
          </template>
        </div>
      </div>
      <div v-else-if="!hasContent && !loading">
        <BaseNotFound :text="t('labels.empty_events_list')">
          <template #icon="{ isDarkTheme }">
            <IconEventNotfound :is-dark-theme="isDarkTheme" />
          </template>
        </BaseNotFound>
      </div>
    </template>
    <template v-else-if="selectedRoundId">
      <ListEvents
        class="!py-6"
        group-by-date
        group-by-key="date"
        root-class="main-container"
        :no-content-text="t('labels.empty_events_list')"
        :main-category-id="league?.id"
        :sub-category-id="selectedRoundId"
      />
    </template>
  </div>
</template>
