<script setup lang="ts">
import { useWindowSize } from "@vueuse/core";

export interface BaseTeamSliderProps {
  showControls?: boolean;
  loading?: boolean;
  items?: any[];
}
const props = withDefaults(defineProps<BaseTeamSliderProps>(), {
  loading: false,
});

const { width: widthViewPort } = useWindowSize();
const IsShowControls = computed(() => {
  if (props.items && props.items?.length)
    return widthViewPort.value < 80 * props.items?.length;
  return false;
});
const slider = ref<HTMLElement | null>(null);
const scrollTo = (element: HTMLElement, scrollPixels: number, duration: number) => {
  const scrollPos = element.scrollLeft + 1;
  if (!((scrollPos === 0 || scrollPixels > 0) && (element.clientWidth + scrollPos + 1 === element.scrollWidth || scrollPixels < 0))) {
    const startTime
          = "now" in window.performance
            ? performance.now()
            : new Date().getTime();

    function scroll(timestamp: number) {
      const timeElapsed = timestamp - startTime;
      const progress = Math.min(timeElapsed / duration, 1);
      element.scrollLeft = scrollPos + scrollPixels * progress;
      if (timeElapsed < duration)
        window.requestAnimationFrame(scroll);
    }
    window.requestAnimationFrame(scroll);
  }
};
const swipeLeft = () => {
  scrollTo(slider.value as HTMLElement, -100, 120);
};
const swipeRight = () => {
  scrollTo(slider.value as HTMLElement, 100, 120);
};
</script>

<template>
  <div class="baseTeamSlider main-container">
    <BaseButton
      v-if="IsShowControls"
      class="baseTeamSlider__button baseTeamSlider__buttonLeft"
      link
      type="secondary"
      name="button-swipe-right"
      @click="swipeLeft()"
    >
      <IconChevronLeft class="relative z-10" />
    </BaseButton>
    <BaseButton
      v-if="IsShowControls"
      class="baseTeamSlider__button baseTeamSlider__buttonRight"
      link
      type="secondary"
      name="button-swipe-right"
      @click="swipeRight()"
    >
      <IconChevronRight class="relative z-10" />
    </BaseButton>
    <div ref="slider" class="baseTeamSlider__content" :class="{ 'mx-12': IsShowControls }">
      <template v-if="props.loading">
        <slot name="skeleton" />
      </template>
      <template v-else-if="props.items && props.items.length">
        <slot v-for="item in props.items" :key="item" :item="item" name="item" />
      </template>
    </div>
  </div>
</template>

<style lang="scss">
.baseTeamSlider {
  @apply flex relative;

  .baseTeamSlider__content {
    @apply flex gap-6 w-full items-center overflow-x-auto h-[5.5rem] overflow-y-hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  .baseTeamSlider__content::-webkit-scrollbar {
    display: none;
  }

  .baseTeamSlider__buttonLeft {
    @apply left-4 md:left-10 xl:left-12 top-[2.3rem];
  }
  .baseTeamSlider__buttonRight {
    @apply right-4 md:right-10 xl:right-12 top-[2.3rem];
  }

  .baseTeamSlider__button {
    @apply  absolute h-8 w-8 flex items-center justify-center
    z-30 overflow-hidden
    transition duration-100;
    color: #fff;
    transform: translateY(35%);

    &::before {
      @apply absolute w-8 h-8 rounded-full;
      content: '';
      background-color: var(--base-card-bgColor);
    }
  }
}
</style>
